$(document).ready(function() {

    // Hover-Click
    $(".item").hover(onIn, onOut);
    $(".item .show").click(function() {
        if ($(this).parents(".item").hasClass("item--active")) {
            $(this).parents(".item").removeClass("item--active")
        } else {
            $(this).parents(".item").addClass("item--active")
        }
    })

    // Burger
    $('.header__burger').click(function () {
        $(this).toggleClass('header__burger--active')
        $('.main-sidebar').toggleClass('main-sidebar--active')
        $('body').toggleClass('fixed-body')
    })

    // Body-bg
    $('.header__check-input').click(function () {
        $('body').toggleClass('body-bg')
    })

    // Delete
    $('.delete').click(function () {
        $(this).parents('.delete-item').addClass('delete-item--active')
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".content-inner").find(".tabs__content").removeClass("tabs__content--active")
        $(this).parents(".content-inner").find(`.tabs__content[data-content-path="${path}"]`).addClass("tabs__content--active")
    })

    // Account
    $('.account__info-btn').click(function () {
        $('.account__info').addClass('account__info--active')
        $('.account__form-none').addClass('account__form-none--active')
    })

    $('.cancel-btn').click(function () {
        $('.account__info').removeClass('account__info--active')
        $('.account__form-none').removeClass('account__form-none--active')
    })

    // Select
    $('.domains-main__select-show').click(function () {
        $(this).siblings('.domains-main__select-hide').toggleClass('domains-main__select-hide--active')
    })

    let show = $('.domains-main__select-show')
    let item = $('.domains-main__select-hide')

    $(document).mouseup(function (e) {
        if ( !show.is(e.target) && show.has(e.target).length === 0 &&
            !item.is(e.target) && item.has(e.target).length === 0

        ) {
            item.removeClass('domains-main__select-hide--active')
        }
    })

    // Range
    $("#demo").ionRangeSlider({
        min: 0,
        max: 10,
    })

    //Calendar

    new AirDatepicker('.datepicker-here', {
        inline: true,
        range: true,
        multipleDatesSeparator: " - ",
        language: 'en',
        locale: {
            days: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
            daysShort: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
            daysMin: ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'],
            months: ['January','February','March','April','May','June', 'July','August','September','October','November','December'],
            monthsShort: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            today: 'Today',
            clear: 'Clear',
            dateFormat: 'mm/dd/yyyy',
            timeFormat: 'hh:ii aa',
            firstDay: 0
        }
    })

    // modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

    // Select
    $(".custom_select select").niceSelect()


    // Payment - radio
    $('.payment__item-check').change(function() {
        if ($(this).is(":checked")) {
            $(this).closest('.payment__item').addClass("payment__item--active");
        } else {
            $(this).closest('.payment__item').removeClass("payment__item--active");
        }
    });

    let checked = null;

    $('.payment__item-check').click(function() {
        checked = checked === this.value ? null : this.value;
        this.checked = !!checked;
        $(this).closest('.payment__item').removeClass("payment__item--active");
    });

    // Password



    // Accordion
    $(".main-sidebar__item-show").click((function(){
    $(this).toggleClass("main-sidebar__item-show--active"),
    $(this).siblings(".main-sidebar__item-hidden").slideToggle()
    }));

    // charts

    // Change
    $('.change').click(function () {
        $('.change__menu').addClass('change__menu--active')
        $('.change__bg').addClass('change__bg--active')
        $('body').addClass('fixed-body')
    })

    $('.change__close').click(function () {
        $('.change__menu').removeClass('change__menu--active')
        $('.change__bg').removeClass('change__bg--active')
        $('body').removeClass('fixed-body')
    })

})


function onIn() {
    if (window.innerWidth > 320) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 320) {
        $(this).removeClass("item--active")
    }
}

